body {
    transition: all 0.5s;
  }
  
  body.dark {
    background-color: #333;
  }
  
  .switch {
    cursor: pointer;
    width: 80px;
    height: 25px;
    /* margin: 80px; */
  }
  
  .switch-enter .button {
    transform: translateX(0);
  }
  
  .switch-enter-done .button {
    transform: translateX(100px);
    transition: transform 0.5s;
  }
  
  .switch-exit .button {
    transform: translateX(100px);
  }
  
  .switch-exit-done .button {
    transform: translateX(0);
    transition: transform 0.5s;
  }
  
  .switch-enter .cloud1 {
    transform: translateX(0);
  }
  
  .switch-enter-done .cloud1 {
    transform: translateX(-100px);
    transition: transform 0.8s;
  }
  
  .switch-exit .cloud1 {
    transform: translateX(-100px);
  }
  
  .switch-exit-done .cloud1 {
    transform: translateX(0);
    transition: transform 0.8s;
  }
  
  .switch-enter .cloud2 {
    transform: translateX(0);
  }
  
  .switch-enter-done .cloud2 {
    transform: translateX(60px);
    transition: transform 0.8s;
  }
  
  .switch-exit .cloud2 {
    transform: translateX(60px);
  }
  
  .switch-exit-done .cloud2 {
    transform: translateX(0);
    transition: transform 0.8s;
  }
  
  .switch-enter .sun {
    transform: translateY(0);
  }
  
  .switch-enter-done .sun {
    transform: translateY(50px);
    transition: transform 0.8s;
  }
  
  .switch-exit .sun {
    transform: translateY(50px);
  }
  
  .switch-exit-done .sun {
    transform: translateY(0);
    transition: transform 0.8s;
  }
  
  .moon {
    transform: translateY(-40px);
  }
  
  .switch-enter .moon {
    transform: translateY(-40px);
  }
  
  .switch-enter-done .moon {
    transform: translateY(0px);
    transition: transform 0.7s;
  }
  
  .switch-exit .moon {
    transform: translateY(0px);
  }
  
  .switch-exit-done .moon {
    transform: translateY(-40px);
    transition: transform 0.7s;
  }
  
